.main{
  padding: 6.12em 0 6.95em;
  &__inner{
    max-width: 45em;
  }
  &__info{
    @include grid100gap(1.44em);
  }
}

.main,
.consult,
.know{
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
}

.main-form{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .9375em));
  gap: 1.875em;
  max-width: 36.625em;
  width: 100%;
  &__btn{
    grid-column: 1/-1;
  }
}

.map{
  height: 25em;
}

.ymaps-2-1-79-balloon__tail,
.ymaps-2-1-79-balloon__tail:after{
  display: none !important;
}

.ymaps-2-1-79-balloon{
  box-shadow: none !important;
  height: 2em;
  transform: translate(93px, 15px);
}

.ymaps-2-1-79-balloon__layout{
  box-shadow: 0 .125em .625em 0 rgba(0, 0, 0, 0.25);
  border-radius: .3125em;
  border: none;
}

.ymaps-2-1-79-balloon__close+.ymaps-2-1-79-balloon__content{
  background: #FFFFFF;
}

.ymaps-2-1-79-balloon__content{
  font-family: $font1;
  font-size: 1.125em !important;
  min-height: unset !important;
  padding: .3125em .625em !important;
  &>*{
    display: flex !important;
    align-items: center;
    font-weight: 700;
    white-space: nowrap;
  }
}

.contacts{
  position: relative;
}

.contacts-info{
  z-index: 3;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  left: 3.4375em;
  background: rgba(255, 255, 255, 0.80);
  padding: 2.8em 3.2em 4.44em;
  width: 27.5em;
  max-width: 100%;
  box-shadow: 0 .125em .625em 0 rgba(0, 0, 0, 0.25);
  @media screen and (min-width: 1401px){
    left: calc(((100vw - (100vw - 100%)) - 1400px + 6.875em) / 2);
  }
  &__items{
    @include grid100gap(1.25em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: 1.12em;
    svg{
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 1.25em;
    }
  }
  &__info{
    & *{
      line-height: inherit;
    }
    a{
      display: block;
    }
  }
  &__days{
    p{
      line-height: inherit;
    }
  }
  .socials{
    margin-top: 1.5em;
    &__item{
      margin: 0 1.25em 0 0;
      &:hover{
        background-color: $primary;
      }
    }
  }
}

.about{
  padding: 6.25em 0;
  background-color: #323643;
  position: relative;
  &__inner{
    background: #FFFFFF;
    display: grid;
    grid-template-columns: 50% 50%;
    overflow: hidden;
    position: relative;
    z-index: 2;
  }
  &__content{
    padding: 2.44em 3em;
    position: relative;
    z-index: 2;
  }
  &__text{
    position: relative;
    z-index: 2;
    margin-top: 1em;
    @include grid100gap(1.25em);
    p{
      line-height: inherit;
    }
  }
  &__bg{
    position: absolute;
    width: 21.25em;
    right: 1.25em;
    bottom: 1.25em;
  }
  &__bg2,
  &__bg3{
    position: absolute;
    width: 38.9375em;
    bottom: 0;
  }
  &__bg2{
    left: 1.25em;
  }
  &__bg3{
    right: 1.25em;
  }
  .video{
    height: 100%;
  }
}

.video{
  width: 100%;
  position: relative;
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
  &__play{
    @include centerAbsolute;
    width: 5em;
    height: 5em;
    border-radius: 50%;
    box-shadow: none;
    img{
      width: 100%;
      display: block;
    }
  }
}

.consult{
  padding: 6.25em 0;
  color: #FFF;
  text-align: center;
  &__content{
    background: rgba(255, 255, 255, 0.10);
    backdrop-filter: blur(7.5px);
    width: 46.25em;
    max-width: 100%;
    padding: 2em 5.31em 3.12em;
  }
  .main-form{
    margin: 2.3em 0 0;
    padding: 0;
    max-width: 100%;
  }
  .fg{
    input{
      border-color: rgba(#fff,.3);
      color: #fff;
      &::placeholder{
        color: #fff;
      }
    }
  }
  &__info{
    @include grid100gap(2em);
  }
  &__top{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: -.12em;
      left: 50%;
      transform: translateX(-50%);
      width: 20.8125em;
      height: 0.9375em;
      background-color: $primary;
    }
    .text44{
      position: relative;
      z-index: 2;
    }
  }
}

.consult2{
  @media screen and (min-width: 577px){
    .consult__content{
      margin-left: auto;
    }
    .consult__info{
      width: calc(100% + 5em);
      margin-left: -2.5em;
    }
  }
}

.opportunities{
  padding: 5.38em 0 6.25em;
  position: relative;
  .m-container{
    position: relative;
    z-index: 2;
  }
  &__items{
    margin-top: 2em;
  }
  &__bg{
    position: absolute;
  }
  &__bg1{
    top: 2.5em;
    left: 2.5em;
    width: 19.5em;
  }
  &__bg2{
    width: 10em;
    top: 6.25em;
    right: 6.25em;
  }
  &__bg3{
    width: 6.54em;
    bottom: 6.25em;
    left: 6.25em;
  }
  &__bg4{
    width: 17.85em;
    right: 2.5em;
    bottom: 2.5em;
  }
}

.opportunity-card{
  position: relative;
  &__img{
    @include imgCover(100%);
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.875em;
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 7.5em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
    }
  }
  &__title{
    text-transform: uppercase;
    line-height: 130%;
    letter-spacing: 0.06em;
  }
}

.catalog{
  padding: 5.37em 0 6.25em;
  position: relative;
  .m-container{
    position: relative;
    z-index: 2;
  }
  &__groups{
    @include grid100gap(5.37em);
  }
}

.catalog-group{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    display: flex;
    align-items: center;
    svg{
      width: 3.4375em;
      height: auto;
      margin-left: 1.25em;
    }
  }
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .625em;
    grid-auto-flow: dense;
  }
  &__item{
    &._big{
      grid-column: span 2;
      .catalog-group__img{
        padding-bottom: 74.9%;
      }
    }
  }
  &__img{
    display: block;
    min-height: 100%;
    @include imgCover(148%);
  }
}

.catalog2{
  padding: 6.25em 0;
  .catalog-group__grid{
    margin-top: 0;
  }
  &__nav{
    margin-top: 1.88em;
    display: flex;
    justify-content: center;
  }
}

.know{
  padding: 5.38em 0 5.8em;
  color: #fff;
  &__content{
    max-width: 42em;
    width: 100%;
    @include grid100gap(2.13em);
  }
  &__text{
    @include grid100gap(1.25em);
    p{
      line-height: inherit;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 2.7em 0 3.25em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__inner{
      max-width: 27em;
    }
  }

  .main-form{
    margin-top: 2.13em;
    max-width: 22.125em;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }

  .map{
    height: 16.25em;
  }

  .contacts-info{
    left: 1.25em;
    padding: 1.62em 1.92em 2.69em;
    width: 22.125em;
    &__items{
      gap: .81em;
    }
    &__icon{
      width: 1.125em;
      margin-right: .7em;
    }
    .socials{
      margin-top: 1.5em;
    }
  }

  .about{
    padding: 3.125em 0;
    &__inner{
      grid-template-columns: calc(100% - 24em) 24em;
    }
    &__bg{
      width: 12.4em;
      bottom: .625em;
      right: .625em;
    }
    &__bg2,
    &__bg3{
      display: none;
    }
    &__content{
      padding: .88em 1.25em;
    }
  }

  .video{
    &__play{
      width: 3.75em;
      height: 3.75em;
    }
  }

  .consult{
    padding: 3.25em 0;
    &__content{
      width: 23em;
      padding: 1.31em 1.6em 1.87em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .main-form{
      margin-top: .94em;
      grid-template-columns: 100%;
    }
    &__top{
      &:before{
        width: 13em;
        height: 0.625em;
      }
    }
  }

  .consult2{
    @media screen and (min-width: 577px){
      .consult__info{
        width: 100%;
        max-width: 14.5em;
        margin: 0 auto;
        gap: .88em;
      }
    }
  }

  .opportunities{
    padding: 2.56em 0 3.125em;
    &__items{
      margin-top: .94em;
    }
    &__bg{
      display: none;
    }
  }

  .opportunity-card{
    &__content{
      padding: 1.06em 1.25em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__icon{
      width: 3.75em;
      margin: 0 0 .25em;
    }
  }

  .catalog{
    padding: 2.56em 0 3.125em;
    &__groups{
      gap: 2.56em;
    }
  }

  .catalog-group{
    &__grid{
      margin-top: 1em;
    }
  }

  .catalog2{
    padding: 3.125em 0;
    &__nav{
      margin-top: 1.25em;
    }
  }

  .know{
    padding: 2.625em 0;
    &__content{
      max-width: 24em;
      gap: 1em;
    }
    &__text{
      gap: 1em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 0;
    .m-container{
      padding: 0;
    }
    &__info{
      padding: .9em 1.25em;
      gap: .44em;
    }
    &__img{
      position: static;
      height: auto;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .main-form{
    padding: 1.25em 1.25em 2.5em;
    margin: 0;
    grid-template-columns: 100%;
  }

  .map{
    height: 13.75em;
  }

  .contacts-info{
    width: 100%;
    padding: 1.25em;
    transform: unset;
    position: static;
    &__items{
      gap: .75em;
    }
    .socials{
      justify-content: unset;
      margin-top: 1em;
      &__item{
        margin-right: 1.875em;
      }
    }
  }

  .ymaps-2-1-79-balloon{
    transform: unset;
  }

  .about{
    padding: 2.5em 0;
    &__inner{
      grid-template-columns: 100%;
    }
    .video{
      height: auto;
    }
    &__bg{
      width: 8.9em;
    }
  }

  .video{
    &__play{
      width: 2.5em;
      height: 2.5em;
    }
  }

  .consult{
    padding: 13.125em 0 2.5em;
    &__content{
      background: rgba(255, 255, 255, 0.05);
      width: 100%;
      padding: 1.5em 1.25em 1.88em;
    }
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__info{
      gap: .94em;
      max-width: 11.5em;
      margin: 0 auto;
    }
    &__top{
      &:before{
        width: 10.125em;
        height: 0.375em;
      }
    }
  }

  .opportunities{
    padding: 2.12em 0 2.5em;
    &__items{
      margin-top: 1em;
    }
  }

  .opportunity-card{
    &__content{
      padding: .625em .5em;
    }
    &__icon{
      width: 2.5em;
      margin-bottom: .44em;
    }
    &__title{
      letter-spacing: 0.0275em;
      font-size: .6875em;
      min-height: 2.6em;
      display: flex;
      align-items: center;
    }
  }

  .catalog{
    padding: 2.13em 0 2.5em;
    &__groups{
      gap: 2.12em;
    }
  }

  .catalog-group{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      margin-top: .7em;
      svg{
        width: 1.8em;
        margin-left: .625em;
      }
    }
    &__grid{
      margin-top: .75em;
      grid-template-columns: 100%;
    }
    &__item{
      &._big{
        grid-column: 1/-1;
      }
    }
    &__img{
      padding: 0 !important;
      img{
        height: auto;
        position: static;
        display: block;
      }
    }
  }

  .know{
    padding: 0 0 2.2em;
    background: #323643;
    &__content{
      max-width: 100%;
      gap: .88em;
      margin-top: .88em;
    }
    &__img{
      position: static;
      height: auto;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }
}
