.text44{
  font-size: 2.75em;
  line-height: 130%;
  letter-spacing: .04em;
  text-transform: uppercase;
}

.text34{
  font-size: 2.125em;
  line-height: 130%;
  letter-spacing: .01em;
}

.text24{
  font-size: 1.5em;
  line-height: 150%;
  letter-spacing: .01em;
}

.m-list li,
.text18{
  font-size: 1.125em;
  line-height: 150%;
  letter-spacing: 0.01em;
}

.font2{
  font-family: $font2;
}

.primary-text{
  color: $primary;
}

.medium-text{
  font-weight: 500;
}


@media screen and (max-width: 992px){
  .text44{
    font-size: 1.75em;
    letter-spacing: 0.07em;
  }

  .text34{
    font-size: 1.25em;
  }

  .text24{
    font-size: 1.125em;
    letter-spacing: 0.01125em;
  }

  .m-list li,
  .text18{
    font-size: .875em;
    letter-spacing: 0.00875em;
  }
}


@media screen and (max-width: 576px){
  .text44{
    font-size: 1.25em;
    letter-spacing: 0.05em;
  }

  .text34{
    font-size: 1em;
    letter-spacing: 0.01em;
  }

  .text24{
    font-size: .875em;
    letter-spacing: 0.00875em;
  }

  .m-list li,
  .text18{
    font-size: 0.6875em;
    letter-spacing: 0.00688em;
  }
}
