.modal-backdrop {
    &.show {
        opacity: 1;
        background: rgba(70, 65, 65, 0.5);
    }
}

.modal{
    text-align: center;
    @media screen and (min-width: 577px){
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
    }

    &__close{
        width: 1.875rem;
        height: 1.875rem;
        position: absolute;
        top: .3125em;
        right: .3125em;
        background-color: #F8F9FA;
        border-radius: 50%;
        svg{
            width: .75em;
            height: auto;
        }
        &:hover{
            line{
                stroke: $primary;
            }
        }
    }
    &__title{
        font-size: 1.25em;
        line-height: 150%;
        text-align: center;
        font-weight: 700;
        text-transform: uppercase;
    }
    &__nav{
        margin-top: 2.25em;
        display: flex;
        justify-content: center;
    }
    &__btn{
        width: 13.75em;
        max-width: 100%;
    }
}

.modal-dialog{
    margin: 1em auto;
    pointer-events: unset;
    width: 34em;
    max-width: calc(100% - 1.5em);
    padding: 4em 2em 3.75em;
    background-color: #fff;
    @media screen and (min-width: 577px){
        display: inline-block;
        vertical-align: middle;
        text-align: left;
    }
}

.login-modal{
    .modal-dialog{
        width: 20em;
        padding: 1.38em 1.25em 1.5em;
    }
    &__top{
        padding-bottom: .2em;
        border-bottom: 1px solid #E4E5E6;
    }
    &__title{
        text-align: center;
        font-size: 1.5em;
        line-height: 130%;
        letter-spacing: 0.01em;
    }
    &__hint{
        text-align: center;
        font-size: 0.875em;
        line-height: 150%;
        color: #6C6D6E;
        letter-spacing: .01em;
    }
}

.modal-form{
    margin: 1.25em 0 1.5em;
    @include grid100gap(1.25em);
}


@media screen and (max-width: 576px){
    .modal{
        &__title{
            font-size: 1em;
        }
        &__nav{
            margin-top: 1.25em;
        }
        &__btn{
            height: 3.125em;
            span{
                font-size: 1em;
            }
        }
    }

    .modal-dialog{
        padding: 3.125em 1em;
    }
}
