.contacts-page{
  padding: 3.125em 0 .625em;
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
    min-height: 31.25em;
  }
  .contacts-info{
    position: static;
    transform: unset;
    box-shadow: unset;
    width: 100%;
  }
  .map{
    height: 100%;
  }
}

.services{
  padding: 1.625em 0 8em;
  &__title{
    text-align: center;
  }
  &__grid{
    margin-top: 2.7em;
  }
}

.service-card{
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(75%);
    margin-bottom: .625em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.69em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .4em));
    gap: .8em;
  }
  &__btn{
    height: 2.625em;
    padding: 0;
    span{
      font-size: .75em;
    }
  }
}

.service-item{
  padding: 1.25em 0 7em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    @include grid100gap(1.625em);
  }
  &__info{
    margin-top: 1.625em;
    @include grid100gap(1.9em);
  }
  &__price{
    text-transform: unset;
    letter-spacing: .01em;
  }
  &__text{
    @include grid100gap(1.25em);
    p{
      line-height: inherit;
    }
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: 2.75em;
  }
  &__btn{
    font-weight: 600;
  }
}

.m-list{
  li{
    display: flex;
    align-items: center;
    font-weight: 600;
    &:before{
      content: '';
      flex-shrink: 0;
      width: .44em;
      height: .44em;
      background-color: $primary;
      margin-right: 1.11em;
    }
  }
}

.certificates{
  .grid-column2{
    row-gap: 3.125em;
  }
}

.certificate-card{
  &__img{
    @include imgCover(38%);
  }
  &__content{
    margin-top: 1.25em;
    display: flex;
    justify-content: space-between;
  }
  &__title{
    span{
      white-space: nowrap;
    }
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 1em;
    width: 8.4375em;
    height: 2.5em;
    padding: 0;
    font-weight: 600;
  }
}

.sales{
  padding: 2.375em 0 3em;
  &__title{
    text-align: center;
    p{
      line-height: inherit;
    }
  }
  &__items{
    margin-top: 2.375em;
    @include grid100gap(4em);
  }
}

.sale-item{
  display: grid;
  grid-template-columns: 25.625em calc(100% - 26.875em);
  gap: 1.25em;
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__img{
    display: block;
    width: 100%;
  }
  &__text{
    margin-top: auto;
    padding-top: 1em;
  }
}

.prices-group{
  margin-top: 1.25em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2em;
  }
}

.gym{
  padding: 2.44em 0 0;
  &__inner{
    @include grid100gap(5.5em);
  }
}

.gym-group{
  &__top{
    padding-bottom: .625em;
    border-bottom: 1px solid #C6C7C8;
  }
  &__grid{
    margin-top: 1.875em;
  }
}

.gym-card{
  background-color: #fff;
  display: grid;
  grid-template-columns: 16.8125em calc(100% - 16.8125em);
  &__img{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 1.25em 1.875em;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  .m-list{
    margin-top: .75em;
  }
  &__nav{
    margin-top: 1.44em;
    display: flex;
  }
  &__btn{
    width: 100%;
    height: 2.5em;
  }
  &._big{
    grid-column: 1/-1;
    grid-template-columns: 50% 50%;
    @media screen and (min-width: 577px){
      .m-list{
        display: grid;
        grid-template-columns: 1.3fr 1fr;
        gap: .5em 1.875em;
      }
      .gym-card__nav{
        margin-top: 1.75em;
      }
      .gym-card__btn{
        width: 18.8125em;
        margin-left: auto;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    padding: 1.25em 0 2.5em;
    &__inner{
      min-height: 12.5em;
    }
  }

  .services{
    padding: 2.4em 0 4em;
    &__grid{
      margin-top: 2em;
      row-gap: 1.5em;
    }
  }

  .service-card{
    &__nav{
      grid-template-columns: 100%;
      gap: .3125em;
      padding-top: .625em;
    }
    &__btn{
      height: 2em;
    }
  }

  .service-item{
    padding: 0 0 3.125em;
    &__top{
      gap: 1em;
    }
    &__info{
      gap: 1em;
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      padding: 0 .5em;
      span{
        letter-spacing: .01em;
      }
    }
  }

  .certificates{
    .grid-column2{
      row-gap: 1.875em;
    }
  }

  .certificate-card{
    &__content{
      margin-top: .82em;
    }
    &__title{
      font-size: .875em;
    }
    &__btn{
      width: 7.8125em;
      height: 1.5em;
      margin-left: .25em;
    }
  }

  .sales{
    padding: 1em 0 4em;
    &__items{
      gap: 2.8em;
    }
  }

  .sale-item {
    grid-template-columns: 12.5em calc(100% - 13.125em);
    gap: .625em;
    .text24{
      font-size: .875em;
    }
  }

  .prices-group{
    margin-top: .75em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .gym{
    padding: 2.9em 0 0;
    &__inner{
      gap: 2.9em;
    }
  }

  .gym-group{
    &__top{
      padding-bottom: .25em;
    }
    &__grid{
      margin-top: 1.25em;
    }
  }

  .gym-card{
    padding: 1.25em;
    grid-template-columns: 7.5em calc(100% - 8.75em);
    gap: 1.25em;
    &__img{
      img{
        height: auto;
      }
    }
    &__content{
      padding: 0;
    }
    .m-list{
      margin-top: .56em;
    }
    &__nav{
      margin-top: .87em;
    }
    &__btn{
      width: auto;
      padding: 0 1.6em;
      height: 1.875em;
      span{
        font-size: .75em;
      }
    }
    &._big{
      padding: 0;
      gap: 0;
      .gym-card__img{
        img{
          height: 100%;
        }
      }
      .gym-card__content{
        padding: .94em 1.25em;
      }
      .m-list{
        gap: .25em 1.5em;
        li{
          line-height: 130%;
        }
      }
      .gym-card__nav{
        margin-top: 1.13em;
      }
      .gym-card__btn{
        width: auto;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts-page{
    padding: 0;
    .m-container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    .map{
      height: 13.75em;
    }
  }

  .services{
    padding: 1em 0 2.5em;
    &__grid{
      margin-top: 1em;
    }
  }

  .service-item{
    padding: .3em 0 2.5em;
    &__inner{
      padding-top: 4.4em;
      position: relative;
    }
    &__top{
      gap: .5em;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
    &__info{
      gap: .69em;
      margin: 0;
    }
  }

  .certificates{
    .grid-column2{
      row-gap: .81em;
    }
  }

  .sales{
    padding: 1em 0 4em;
    &__items{
      gap: 1em;
      margin-top: 1.875em;
    }
  }

  .sale-item {
    grid-template-columns: 100%;
    gap: .25em;
    &__text{
      br{
        display: none;
      }
    }
  }

  .prices-group{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    .text24{
      font-size: .6875em;
    }
  }

  .gym{
    padding: 2.19em 0 0;
    &__inner{
      gap: 2.19em;
    }
  }

  .gym-group{
    &__top{
      padding-bottom: .31em;
    }
  }

  .gym-card{
    padding: 1.25em .625em;
    grid-template-columns: 6.25em calc(100% - 7.5em);
    .m-list{
      @include grid100gap(.5em);
      li{
        line-height: 130%;
      }
    }
    &._big{
      grid-template-columns: 100%;
    }
  }
}
