@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 150%;
        letter-spacing: 0.01125em;
    }
}


.m-btn-black-outline{
    border: 1px solid rgba($black,.3);
    color: $black;
    padding: 0 1.88em;
    height: 3.75em;
    &:hover{
        background-color: $primary;
        border-color: $primary;
        color: #fff;
    }
}

.m-btn-white-outline{
    padding: 0 1.88em;
    height: 2.5em;
    border: 1px solid rgba(#fff,.3);
    color: #fff;
    &:hover{
        background-color: #fff;
        border-color: #fff;
        color: $primary;
    }
}

.m-btn-primary{
    padding: 0 1.88em;
    height: 3.75em;
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.5em;
        span{
            font-size: .875em;
        }
    }

    .m-btn-black-outline{
        padding: 0 1.25em;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        span{
            font-size: .6875em;
        }
    }
}
