.breadcrumbs {
    padding: 1.5em 0;
    &__item {
        font-size: .875em;
        line-height: 150%;
        position: relative;
        font-weight: 400;
        display: inline;
        letter-spacing: 0.01em;
        &:not(:first-child) {
            &:before {
                content: ' / ';
                margin: 0 .25em;
            }
        }
    }
}

.page-top{
    padding: 1.5em 0 4.25em;
    background-size: cover;
    background-position: center;
    color: #fff;
    position: relative;
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(50, 54, 67, 0.70) 0%, #323643 100%);
    }
    &__inner{
        @include grid100gap(1.875em);
        position: relative;
        z-index: 2;
    }
    .breadcrumbs{
        padding: 0;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1em 0;
        &__item{
            font-size: 0.6875em;
        }
    }

    .page-top{
        padding: 1em 0 3.44em;
        &__inner{
            gap: 1.625em;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        padding: 1em 0 .625em;
        &__item{
            font-size: 0.5625em;
        }
    }

    .page-top{
        padding-bottom: 1.625em;
        &__inner{
            gap: 1.125em;
        }
    }
}
