.spa{
  padding: 2.25em 0 6.25em;
  &__grid{
    margin-top: 2.25em;
  }
}

.spa-card{
  background-color: #fff;
  display: flex;
  flex-direction: column;
  &__img{
    flex-shrink: 0;
    @include imgCover(74.9%);
  }
  &__content{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 1.2em 1.875em 1.875em;
  }
  .m-list{
    margin: .8em 0;
  }
  &__prices{
    margin-top: .75em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__btn{
    width: 100%;
  }
}

.order{
  padding: .375em 0 6.25em;
  &__inner{
    margin-top: 2em;
    padding: 0 1.875em 1.875em;
    background-color: #fff;
  }
  &__bottom{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.375em;
    .text18{
      margin-bottom: .1em;
    }
  }
  &__btn{
    padding: 0 1.875em;
    margin-top: 1.25em;
  }
}

.order-card{
  padding: 1.875em 1.3em;
  border-bottom: 1px solid #E4E5E6;
  display: grid;
  grid-template-columns: calc(100% - 32.125em) 6em 8.75em 8em 1.875em;
  gap: 1.875em;
  align-items: center;
  &__col{
    display: flex;
    align-items: center;
  }
  &__price1{
    text-align: right;
  }
  &__remove{
    padding-bottom: 100%;
    position: relative;
    background-color: #F8F9FA;
    width: 100%;
    border-radius: 50%;
    svg{
      @include centerAbsolute;
      width: .75em;
      height: auto;
    }
  }
}

.cartcalc{
  padding: 0 .3125em;
  display: grid;
  grid-template-columns: 1.875em calc(100% - 3.75em) 1.875em;
  height: 2.5em;
  align-items: center;
  border: 1px solid #E4E5E6;
  background-color: #F8F9FA;
  input{
    width: 100%;
    height: 100%;
    border: none;
    background-color: transparent;
    font-size: 1.125em;
    letter-spacing: 0.01em;
    text-align: center;
  }
  button{
    padding-bottom: 100%;
    background-color: #fff;
    position: relative;
    svg{
      @include centerAbsolute;
      width: 1em;
      height: auto;
    }
  }
}

.order-form{
  margin-top: 2em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__col{
    background-color: #fff;
    padding: 1.375em 1.875em;
    display: flex;
    flex-direction: column;
  }
  &__top{
    flex-shrink: 0;
    padding-bottom: .81em;
    border-bottom: 1px solid #E4E5E6;
    margin-bottom: 1.25em;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  .fg{
    textarea,
    input{
      border-color: #E4E5E6;
      &:focus{
        border-color: $primary;
      }
    }
    input{
      &::placeholder{
        color: #6C6D6E;
      }
    }
  }
  &__count{
    margin-top: -.5em;
    @include grid100gap(.2em);

  }
  .cartcalc{
    height: 3.75em;
    padding: 0 .625em;
  }
  &__comment{
    flex-grow: 1;
    textarea{
      height: 100%;
    }
  }
  .order__bottom{
    margin-top: 1.94em;
  }
  .order__btn{
    margin-top: 0;
  }
}

.pay-way{
  margin-top: .38em;
  &__items{
    margin-top: .38em;
  }
  &__item{
    position: relative;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      cursor: pointer;
      opacity: 0;
      &:hover+label,
      &:checked+label{
        background-color: rgba($primary,.5);
        border: none;
      }
    }
    label{
      transition: all .3s;
      height: 2.5em;
      @include center;
      border: 2px solid rgba($primary,.3);
    }
  }
}


@media screen and (max-width: 992px){
  .spa{
    padding: 1.38em 0 3.125em;
    &__grid{
      margin-top: 1.38em;
    }
  }

  .spa-card{
    &__content{
      padding: .8em 1.25em 1.25em;
    }
    .m-list{
      margin: .6em 0;
    }
    &__prices{
      margin-top: .6em;
    }
    &__nav{
      padding-top: .875em;
    }
  }

  .order{
    padding: .625em 0 3.125em;
    &__inner{
      margin-top: 1em;
      padding: 0 1.25em 1.25em;
    }
    &__bottom{
      margin-top: .875em;
    }
    &__btn{
      padding: 0 1.25em;
      margin-top: .875em;
    }
  }

  .order-card{
    padding: .625em 1.25em;
    grid-template-columns: calc(100% - 22.5em) 5em 6.25em 5em 1.25em;
    gap: 1.25em;
    &__remove{
      svg{
        width: .5em;
      }
    }
  }

  .cartcalc{
    padding: 0 .1875em;
    grid-template-columns: 1.5em calc(100% - 3em) 1.5em;
    height: 1.875em;
    input{
      font-size: .875em;
    }
    button{
      svg{
        width: .875em;
      }
    }
  }

  .order-form {
    margin-top: 1em;
    &__grid {
      grid-template-columns: repeat(2, calc(50% - .625em));
      gap: 1.25em;
    }
    &__col{
      padding: .81em 1.25em 1.25em;
    }
    &__top{
      padding-bottom: .25em;
    }
    ._full{
      grid-column: 1/-1;
    }
    .cartcalc{
      height: 2.5em;
    }
    &__count{
      margin-top: -.3em;
    }
    .order__bottom{
      margin-top: 5em;
    }
  }

  .pay-way{
    margin-top: .94em;
    &__items{
      margin-top: .83em;
    }
    &__item{
      label{
        height: 2.1875em;
      }
      .text18{
        font-size: 1.125em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .spa {
    padding: .94em 0 2.5em;

    &__grid {
      margin-top: .95em;
    }
  }

  .order{
    padding: 0 0 2.5em;
    &__btn{
      width: 100%;
      height: 1.875em;
    }
  }

  .order-card{
    padding: 1em 0;
    grid-template-columns: 100%;
    gap: 0;
    position: relative;
    &__remove{
      position: absolute;
      top: .625em;
      right: -.625em;
      width: 1.25em;
      height: 1.25em;
      padding: 0;
      svg{
        width: .5em;
      }
    }
    .cartcalc{
      margin: .25em 0;
      width: 6.25em;
    }
  }

  .order-form{
    &__col{
      display: block;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__count{
      grid-column: 1/-1;
    }
    &__comment{
      height: 6.25em;
    }
    .order__bottom{
      margin-top: 1.2em;
      padding: 0 1.25em;
    }
    .order__btn{
      height: 2.5em;
    }
  }

  .pay-way{
    &__items{
      margin-top: 1em;
    }
    &__item{
      .text18{
        font-size: .875em;
      }
    }
  }
}
