@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.socials{
  display: flex;
  align-items: center;
  &__item{
    @include center;
    width: 3.125em;
    height: 3.125em;
    flex-shrink: 0;
    margin-right: 1.25em;
    border-radius: 50%;
    background: $primary;
    img{
      width: 1.625em;
      filter: drop-shadow(0px 1px 2px rgba(0, 0, 0, 0.10));
    }
    &:hover{
      background: darken($primary,20%);
    }
  }
}

.grid-column2,
.grid-column3{
  display: grid;
  gap: 1.875em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .9375em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
}


@media screen and (max-width: 992px){
  .socials{
    &__item{
      width: 1.875em;
      height: 1.875em;
      img{
        width: .9375em;
      }
    }
  }

  .grid-column2{
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }

  .grid-column3{
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .socials{
    justify-content: center;
    width: 100%;
    margin-top: 1.375em;
    &__item{
      margin-right: .9375em;
      width: 2.5em;
      height: 2.5em;
      img{
        width: 1.25em;
      }
    }
  }

  .grid-column2{
    grid-template-columns: 100%;
    gap: 1em;
  }

  .grid-column3{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }
}
