.header{
  background-color: #fff;
  padding-top: 4.375em;
  position: relative;
  &__inner{
    padding: 2.31em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 15.8em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.m-nav{
  display: flex;
  align-items: center;
  &__link{
    text-transform: uppercase;
    font-family: $font2;
    position: relative;
    @include lastM(0 2.19em 0 0);
    &:hover,
    &._active{
      color: $black;
      &:before{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      width: calc(100% + .625em);
      height: .625em;
      background-color: rgba($primary,.3);
      transition: inherit;
      opacity: 0;
    }
    span{
      position: relative;
      z-index: 2;
      font-size: 1em;
      line-height: 150%;
      letter-spacing: 0.04em;
    }
  }
}

.header-contacts{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: $primary;
  &__inner{
    height: 4.375em;
    display: flex;
    align-items: center;
    padding-right: 5.125em;
  }
  &__item{
    display: flex;
    align-items: center;
    opacity: .5;
    transition: all .3s;
    color: #fff;
    @include lastM(0 3.8em 0 0);
    &:hover{
      opacity: 1;
      color: #fff;
      cursor: pointer;
    }
    img{
      max-width: 1em;
      max-height: 1em;
      margin-right: .67em;
      flex-shrink: 0;
    }
    span{
      font-size: 1.125em;
      line-height: 150%;
      letter-spacing: 0.01125em;
    }
  }
  &__btn{
    margin-left: auto;
  }
}

.header-cart{
  z-index: 3;
  position: absolute;
  top: 0;
  right: 3.4375em;
  height: 4.375em;
  width: 3.125em;
  display: flex;
  align-items: center;
  @media screen and (min-width: 1440px){
    right: calc((100vw - 1440px) / 2 + 3.4375em);
  }
  svg{
    width: 100%;
    height: auto;
  }
  &__pin{
    top: .75em;
    right: -.5em;
    position: absolute;
    min-width: 1.25em;
    height: 1.25em;
    padding: 0 .25em;
    @include center;
    border-radius: 50%;
    background-color: #fff;
    color: $primary;
    margin-left: .3125em;
    text-shadow: 0 .0625em .125em rgba(0,0,0,.1);
    span{
      font-size: .75em;
      letter-spacing: .03em;
    }
  }
}

.menu-nav,
.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 3.125em;
    &__inner{
      padding: 0;
      justify-content: flex-end;
    }
    &__logo{
      width: 13.7em;
      margin-right: auto;
    }
    &._open{
      .menu-nav,
      .m-nav,
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-contacts{
    &__inner{
      height: 3.125em;
      padding-right: 0;
      justify-content: space-between;
    }
    &__item{
      @include lastM(0);
      span{
        font-size: .875em;
      }
    }
    &__btn{
      @media screen and (min-width: 577px){
        position: absolute;
        right: 8.15em;
        top: 100%;
        margin-top: 1em;
        border-color: rgba(0, 0, 0, 0.30);
        color: $black;
        &:hover{
          border-color: $primary;
          background-color: $primary;
          color: #fff;
        }
      }
    }
  }

  .header-cart{
    position: relative;
    top: unset;
    right: unset;
    path{
      fill: $primary;
    }
    &__pin{
      background-color: $primary;
      color: #fff;
    }
  }

  .burger{
    display: flex;
    width: 2.5em;
    flex-shrink: 0;
    margin-left: .875em;
    img{
      width: 100%;
    }
  }

  .menu-nav{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba($primary,.9);
    padding: 3.125em 1.5em 0;
    z-index: 20;
    transition: all .4s;
    opacity: 0;
    pointer-events: none;
    &__close{
      background-color: transparent;
      width: 1.875em;
      height: 4.375em;
      @include center;
      img{
        width: 100%;
      }
    }
  }

  .m-nav{
    flex-direction: column;
    @media screen and (min-width: 577px){
      position: fixed;
      height: calc(100 * var(--vh) - 7.5em);
      width: 100%;
      background-color: #fff;
      left: 0;
      top: 7.5em;
      z-index: 20;
      opacity: 0;
      pointer-events: none;
      transition: all .4s;
      overflow-y: auto;
    }
    &__link{
      margin: 0 !important;
      width: 100%;
      height: 2.5em;
      border-bottom: 1px solid #E4E5E6;
      @include center;
      &:before{
        width: 6em;
        left: 50%;
        bottom: .75em;
        transform: translate(-50%,0);
        height: 0.375em;
      }
      span{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 0;
    &__logo{
      width: 11.4em;
    }
  }

  .header-cart{
    width: 2.5em;
  }

  .menu-nav{
    height: 4.375em;
    align-items: center;
    padding-top: 0;
  }

  .menu{
    position: fixed;
    height: calc(100 * var(--vh) - 4.375em);
    width: 100%;
    background-color: #fff;
    left: 0;
    top: 4.375em;
    z-index: 20;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;
    transition: all .4s;
    display: flex;
    flex-direction: column;
  }

  .header-contacts{
    position: static;
    flex-grow: 1;
    padding: 2em 0;
    &__inner{
      height: unset;
      flex-direction: column;
      justify-content: unset;
    }
    &__item{
      @include lastM(0 0 1.2em);
    }
    &__btn{
      flex-shrink: 0;
      width: 15em;
      order: -1;
      margin: 0 auto 2.3em;
      span{
        font-size: .875em;
      }
    }
  }
}
