.error,
.thank{
  &__inner{
    background: #FFF;
    text-align: center;
    position: relative;
  }
  &__nav{
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 2;
  }
  &__btn{
    width: 15.875em;
    max-width: 100%;
  }
  .breadcrumbs{
    text-align: left;
  }
}

.error{
  padding: 3.25em 0;
  &__inner{
    padding: 18em 2em 6em;
  }
  &__title{
    font-size: 2.875em;
    line-height: 130%;
    font-weight: 600;
    letter-spacing: .01em;
  }
  &__img{
    position: absolute;
    width: 43em;
    top: 10.65em;
    left: 50%;
    transform: translateX(-50%);
  }
  &__info{
    @include grid100gap(.44em);
    text-align: center;
    position: relative;
    z-index: 2;
  }
  &__nav{
    margin-top: 8.75em;
  }
  &__desc{
    span{
      font-size: 1.25em;
      line-height: 130%;
      letter-spacing: 0.0125em;
    }
  }
}

.thank{
  padding-bottom: 3.8em;
  text-align: center;
  &__desc{
    font-size: 2.5em;
  }
  &__inner{
    padding: 12em 2em 7.6em;
  }
  &__info{
    @include grid100gap(1.4em);
  }
  &__nav{
    margin-top: 2.62em;
  }
  .socials{
    justify-content: center;
    margin: 2.44em 0 .44em;
    &__item{
      background-color: $primary;
      margin: 0 .625em;
    }
  }
  &__hint{
    font-size: 1em;
    line-height: 150%;
    letter-spacing: .01em;
  }
}



@media screen and (max-width: 992px){
  .error{
    padding: 2em 0;
    &__img{
      width: 22.625em;
      top: 3.87em;
    }
    &__inner{
      padding: 8.75em 1em 3em;
    }
    &__nav{
      margin-top: 4.37em;
    }
    &__title{
      font-size: 1.625em;
    }
    &__desc{
      span{
        font-size: .75em;
      }
    }
  }

  .thank{
    padding-bottom: 2.75em;
    &__desc{
      font-size: 1.5em;
    }
    &__inner{
      padding: 9.75em 2em 6.25em;
    }
    &__info{
      gap: 1.6em;
    }
    &__nav{
      margin-top: 4.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .error{
    padding: 1.5em 0;
    &__img{
      width: 10.875em;
      top: 2.2em;
    }
    &__inner{
      padding: 3.4em 1.25em 10em;
    }
    &__nav{
      margin-top: 3em;
    }
    &__title{
      font-size: 1.25em;
      max-width: 90%;
      margin: 0 auto;
    }
    &__desc{
      span{
        font-size: .6875em;
      }
    }
  }

  .thank{
    padding-bottom: 1.5em;
    &__title{
      font-size: 1.125em;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
    &__inner{
      padding: 9.6em .5em 2.25em;
    }
    &__info{
      gap: 1.25em;
    }
    &__hint{
      width: 62%;
      margin: 0 auto;
    }
  }
}
