.fg {
  position: relative;
  margin: 0;
  min-height: unset;
  textarea,
  input {
    border: $border;
    padding: 0 1em;
    height: 3em;
    width: 100%;
    font-size: 1.125em;
    line-height: 150%;
    background-color: transparent;
    color: $black;
    @include sameNumWidth;
    &::placeholder {
      color: $black;
    }
    &:focus{
      border-color: $primary;
    }
  }
  textarea{
    height: 9.375em;
    padding-top: .5em;
    padding-bottom: .5em;
    resize: none;
  }
  &__icon{
    position: absolute;
    top: .625em;
    width: 2.88em;
    height: 1.875em;
    @include center;
    img{
      max-width: 1em;
      max-height: 1em;
    }
    &~input{
      padding-left: 2.3em;
      padding-right: .5em;
    }
  }
  .invalid-feedback{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .25em;
    font-size: .75em;
  }
}


@media screen and (max-width: 992px){
  .fg{
    input,
    textarea{
      font-size: .875em;
      height: 2.86em;
    }
    &__icon{
      height: 2.5em;
      width: 2em;
      top: 0;
      &~input{
        padding-left: 2em;
      }
      img{
        max-width: .75em;
        max-height: .75em;
      }
    }
  }
}
